import React from "react";
import { Card,  } from "@material-ui/core";
import { Container, Row, Col, CardBody } from "reactstrap";


const Main = (props) => {  
  return (
        <Container fluid="true" className="mt-5 p-0 m-0">
          <Row className="m-0 p-0">
            <Col md={12} className="pl-0 ml-0 pr-0 mr-0">
              <Card>
                 <CardBody>
                    Travel Schedules 
                 </CardBody>
              </Card>
            </Col>
          </Row>
        </Container>
  );
};

export default Main;
